.services{
grid-column: col-start 1 / span 12;
display:grid;
grid-template-columns: 1f;
grid-template-rows:auto;

//justify-items:center;
grid-gap:3rem;


@include responsive(tablet){
grid-template-columns: 1fr 1fr 1fr;
}

@include responsive(laptop){
grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
min-height:100vh;
}


& > section:nth-child(n + 2):nth-child(-n + 7){
background:$color-primary;
min-height:450px;
border-radius: $header-b-rad;
padding: 1.2rem;
text-align:center;
@include text-template($small,$font-family-ads, $color-primary);



& > .icon-service{
font-size:40px;
color:$bh-green-1;
margin:30px 0px 0px 0px;
padding:0px;
}


& > h2{
color:$color-dark;
margin-top:0px;
}

& > p{
@include text-template($small,$font-family-ads, $color-dark);
}
}//end section nth-child(n + 2)

& > section:nth-child(n + 2):nth-child(-n + 7):hover {
//box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.9);
//transform: scale(1.05);
}

& > .s_header{
grid-column: 1;
grid-row:1;
width:100%;
background:$color-primary;
text-align:center;
min-height:0px;
@include text-template($small,$font-family-ads, $color-primary);
background:$color-band;
@include responsive(tablet){
grid-column: 1 / span 3;
}
@include responsive(laptop){
grid-column: 1 / span 5;
}

& h2{
margin-top:15px;
color:$color-primary;
}

}//header

& > .development{
grid-column: 1;
grid-row:2;
@include responsive(tablet){
}
@include responsive(laptop){
grid-column: 2;
}
}//end development


& > .web-starter{
grid-column: 1;
grid-row:3;
@include responsive(tablet){
grid-column: 2;
grid-row: 2;
}
@include responsive(laptop){
grid-column: 3;
}
}//end web-starter


& > .troubleshoot{
grid-column: 1;
grid-row:4;
@include responsive(tablet){
grid-column: 3;
grid-row: 2;
}
@include responsive(laptop){
grid-column: 4;
}
}//end troubleshoot

& > .design{
grid-column: 1;
grid-row:5;
@include responsive(tablet){
grid-column: 1;
grid-row: 3;
}
@include responsive(laptop){
grid-column: 2;
//grid-row: 12;
}
}//end development


& > .content{
grid-column: 1 ;
grid-row:6;
@include responsive(tablet){
grid-column: 2;
grid-row: 3;
}
@include responsive(laptop){
grid-column: 3;
}
}//end web-starter


& > .refactoring{
grid-column: 1;
grid-row:7;
@include responsive(tablet){
grid-column: 3;
grid-row: 3;
}
@include responsive(laptop){
grid-column:4;
}
}//end refactor


& > .contact{
grid-column: 1;
grid-row:8;
margin-bottom:1rem;
display:flex;
justify-content:center;

@include responsive(tablet){
grid-column:2;
grid-row: 4;
}
@include responsive(laptop){
grid-column:3;
grid-row: 4;
}

& > a{
}

}//end contact

}//end services
