.header {
//grid cell
grid-column: col-start/span 12;
grid-row: 2;
//grid template
display:grid;
grid-template-columns:1fr;
grid-template-rows:auto;
//dimensions
height:93vh;
//background
overflow:hidden;
background-size:cover;
background-repeat: no-repeat;
background-position: center center;

// media queries
@include responsive(small-phone){
grid-column: col-start/span 12;
grid-row: 2;
}
@include responsive(phone){
grid-column: col-start/span 12;
grid-row:2;
}
//768
@include responsive(tablet){
grid-row:1;
grid-template-columns:1fr 1fr;
}

//logo
.logo_box{
grid-column: 1;
grid-row:1;
display:grid;
grid-template-columns:1fr;
grid-template-rows:1fr 1fr 1fr;


.logo_box__logo {
@include background(none, $color-primary,.90, contain);
grid-column:1;
grid-row:1;
width:50%;
height:50%;
align-items:center;
justify-content:center;
display:flex;
border-radius: $header-b-rad;
//justify-self:center;
margin-top:10%;
margin-left:10%;

&__img{
//max-width:100%;
//margin:auto;
padding:0;
//width:70%;
width:80%;
height:80%;
object-fit:contain;
//height:200px
}
}//end image

&__tagline{
font-family: "Open Sans", Arial, sans-serif;
font-weight: 300;
//padding: 10px 5px 10px 5px !important;
grid-column:1;
grid-row:3;
justify-self:center;
width:90%;
border-radius: $header-b-rad;
@include background(none, $color-grey,.7, none);
@include text-template($small,$font-family-ads, $color-primary);
height:15%;
display:flex;
align-items:center;
justify-content:center;
@include responsive(tablet){
//display:grid;
width:60%;
justify-self:end;
}
}
}// end logo-box

//banner box
.banner_box {
display:none;
@include text-template($small,$font-family-ads, $color-dark);
//banner box media query
@include responsive(tablet){
display:grid;
}

//banner-grid
.banner{
grid-template-columns:2fr 1.2fr;
grid-template-rows:auto;
//this variable below controls transparency for entire box
@include background(none, $color-primary,1, none);
display:grid;
line-height:2;
height: auto;
width:90%;
border-radius: $header-b-rad;
padding: 1.2rem;
margin:auto;
align-self:start;

&__head {
grid-column: 1 / span 2 ;
grid-row:1;
//border-radius: $header-b-rad;
@include background(none, $color-primary,$banner-screen, none);
}

&__row {
grid-column:1 /span 2 ;
//background:red;
}

&__check{
grid-column:1;
grid-row:2;
@include responsive(tablet){
grid-column: 1;
@include background(none, $color-primary,$banner-screen, none);
//border-radius: $header-b-rad;
}

& > ul > li > .icon-banner{
@include icons;
margin-left:5px;
}

}//end check

&__image{
//display:none;
@include background($banner-img, $color-primary, 0 , contain);
//background:$color-primary;
//border-radius: $header-b-rad;
@include responsive(tablet){
display:block;
grid-column:2;
grid-row:2;

}//responsive

}//end banner image

&__call{
display:block;
grid-column:1 / span 2;
grid-row:3;
@include background(none, $color-primary,$banner-screen, none);
//border-radius: $header-b-rad;

.phone-splash{
color:$color-purple ;
font-size:$xlarge;
}
}
}//end banner-grid

}// end banner-box




}//end header
