.mission{
grid-column: col-start 1 / span 12;
display:grid;
grid-template-columns: repeat(12, [col-start] 1fr);
grid-template-rows:auto;

@include responsive(small-phone){
grid-column: col-start / span 12;
//grid-row: 12;
}

@include responsive(phone){
grid-column: col-start 1 / span 12;
}

@include responsive(tablet){
//grid-template-columns: 1fr 1fr;
}

// navigation in mission grid no grid
&__navigation{
grid-column: col-start 1/span 12;
grid-row:1;
background:$color-primary;
margin:2rem;
border-radius:1rem;
@include responsive(tablet){
grid-column: col-start 1 / span 5;
padding:1rem;
}//end tablet

& > .nav-tabs > li > a{
border-bottom: 1px solid #ccc;
letter-spacing: 2px;
font-size: 13px;
text-transform: uppercase;
font-weight: 700;
color: #888;
}

& > .tab-content{
transition: 0.5s;
margin-block-start: 1em;
margin-block-end: 1em;
margin-inline-start: 0px;
margin-inline-end: 0px;
font-family: "Open Sans", Arial, sans-serif;
font-weight: 300;
color: #949494;

& > .tab-pane{

& > h2{
line-height: 1.8;
font-size: 21px;
} //end h2

& > p{
font-size: 18px;
}

& > ul{
width:100%;

& > li{
font-size: 18px;
line-height: inherit;

& > .icon-mission{
@include icons;
}//end mission
}//end li
}// ul

}//end tab pane
}//end tab content

}//end navigation

//in mission grid has grid
&__brands{
grid-column: col-start 1/span 12;
grid-row:2;
display:grid;
grid-template-columns:1fr 1fr;
grid-template-rows:auto;
grid-gap:2rem;
margin:2rem;
@include responsive(tablet){
grid-column: col-start 6 / span 12;
grid-row: 1;
}

&__wp{
grid-column:1 / span 2;
grid-row:1;
background:$color-primary;
border-radius:$header-b-rad;
@include responsive(tablet){
grid-column: 1;
grid-row: 1;
}
}//wp

&__avada{
grid-column:1 / span 2;
grid-row:2;

background:$color-primary;
border-radius:$header-b-rad;

@include responsive(tablet){
grid-column: 2;
grid-row: 1;
}
}//avada

&__woocom{
grid-column:1 / span 2;
grid-row:3;
background:$color-primary;
border-radius:$header-b-rad;

@include responsive(tablet){
grid-column: 1;
grid-row: 2;
}
}//woocom

&__hugo{
grid-column:1 / span 2;
grid-row:4;
background:$color-primary;
border-radius:$header-b-rad;

@include responsive(tablet){
grid-column: 2;
grid-row: 2;
}

}
}//end brands




}//end mission
